.row {
    margin-left: 5px;
    color: #ffff;
}
.row-top {
    padding-top: 60px;
}

.row .title, .row-top .title {
    font-size: 1.6rem;
    color: #8843F2;
}


.parent{
    overflow-x: scroll;
    overflow-y: hidden;
    display: flex;
}

.posters {
    padding: 20px 5px;
    flex: 1
}



.movie-title{
    font-size: 1rem ;
    font-weight:400;
    color: #FFF8F3;
    padding-top: 10px;
}

::-webkit-scrollbar{ 
    display: none;
}

.posters img {
    max-height:250px;
    max-width: 350px; 
    margin-right: 8px !important;
    border-radius: 5px;
}

.posters img:hover {
    transform: scale(1.08);
}

@media (max-width: 426px) {
    .posters img {
        max-width: 200px; 
    }
}

@media (max-width: 321px) {
    .posters img {
        max-width: 140px; 
        max-height:150px;
    }
}