


.nav-link{
    color: #fff !important;
}
.logo{
    width: 100px;
}

.nav-size{
    font-size: 1.2rem;
}

.avatar{
    width: 30px;
}

.dropdown-menu[data-bs-popper] {
    left: -100px !important;
}

.bg-dark{
    background: none !important;
}

.navbar-toggler {
    background: #1a1a1acf;
    border: none !important;
}

.nav-black{
    background: #111 !important;
}

.navbar-collapse {
    position: relative;
    top: 3px;
}

.me-4{
    padding-bottom: 10px ;
}

@media (max-width: 992px) {
    .dropdown-menu[data-bs-popper] {
        left: 0px !important;
    }

    .bg-dark{
        background: #111 !important;
    }
}