.banner {
    
    background-size: cover;
    background-repeat: no-repeat;
    height: 800px;
    max-width: 100%;
    color: #ffff;
    /* margin-top: -4rem !important; */
}

.contents {
    padding-top: 200px;
    padding-left: 20px;
    width: 50%;
    height: 190px;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    position: relative;
    z-index: 9;
}

.contents .movie-title{
    font-size: 3rem;
    padding-bottom: 1rem;
    font-weight: 800;
    color: #ffff;
}

.contents .description {
    font-size: 1.2rem;
    padding-top: 1rem;
}

.banner-buttons .button{
    outline: none;
    border: none;
    color: #ffff;
    margin-right: 1rem !important;
    background: rgb(128, 126, 126);
    border-radius: 5px;
    padding: 0.5rem 2rem !important;
    text-align: center;
    display: inline-block;
    font-weight: bold;
    position: relative;
    
}

.banner-buttons .button:hover {
    background: #fff;
    color: #000;
}

.fade:not(.show) {
    opacity: 1 !important;
}

.fade {
    background-color: rgba(0, 0, 0, 0.452);
    top: -200px;
    left: 0;
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 1;

}

/* @media (max-width: 994px) {
    .banner{
        margin-top: -4rem !important;
    }
} */

@media (max-width: 768px) {
    .contents {
        width: 75%;
    }

    .contents .movie-title{
        font-size: 2rem;
    }
    
    .contents .description {
        font-size: 1rem;
        font-weight: 400;
    }

}

@media (max-width: 457px) {

    .banner-buttons .button{
        padding: 0.5rem 1rem !important;
        margin-top: 1rem !important;
        font-size: .8rem;
    }

    .contents .description {
        font-size: .8rem;
        font-weight: 400;
    }
}

@media (max-width: 426px) {
    .contents {
        width: 100%;
    }
}